import React from "react"
import useSearchModalStore from "Stores/useSearchModal"

const Search = () => {
  const open = useSearchModalStore(state => state.open)
  // margin-right: 40px;
  // z-index: 300;
  return (
    <>
      <button onClick={() => open()} className="z-[300] ml-auto mr-10">
        <label
          htmlFor="default-search"
          className="text-sm font-medium text-gray-900 sr-only dark:text-gray-300"
        >
          Search
        </label>
        <div className="relative cursor-pointer">
          <div className="absolute inset-y-0 left-0 flex items-center pl-3 pointer-events-none">
            <svg
              aria-hidden="true"
              className="w-5 h-5 text-gray-500 dark:text-gray-400"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
              ></path>
            </svg>
          </div>
          <input
            type="search"
            id="default-search"
            className="block w-full py-2 pl-10 pr-4 text-sm text-gray-900 border border-gray-300 rounded-lg cursor-pointer bg-gray-50 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white "
            placeholder="Search"
            disabled
          />
          <div className=" absolute right-2.5 top-1/2 transform -translate-y-1/2   font-medium rounded-lg text-sm pl-4 py-2   text-gray-800 dark:text-gray-100">
            <kbd className="px-2 py-1.5 text-xs font-semibold  bg-gray-100 border border-gray-200 rounded-lg dark:bg-gray-600  dark:border-gray-500">
              Ctrl
            </kbd>{" "}
            +{" "}
            <kbd className="px-2 py-1.5 text-xs font-semibold  bg-gray-100 border border-gray-200 rounded-lg dark:bg-gray-600  dark:border-gray-500">
              K
            </kbd>
          </div>
        </div>
      </button>
    </>
  )
}

export default Search
